<template>
  <div class="day bounceInRight" rel="dom">
    <h1>{{data.title}}</h1>
    <div v-html="data.content"></div>
  </div>

</template>

<script type="text/ecmascript-6">
  import vImg from '../lazyloadimg/lazyimg.vue';
  export default {
    name: 'v-day',
    props: {
      data: {
          // prop 验证，值允许接受对象类型的数据
          type: Object
      }
    },
    data() {
      return {
        content: false
      };
    },
    components: {
        // 组件注册
      vImg
    },
    created() {
      this.clearStyle();
    },
    methods: {
      clearStyle() {
        this.$nextTick(() => {
          let tags = this.$el.getElementsByTagName('img');// 获取img的节点
//          console.log('打印img节点');
//          console.log(tags);
          for (let i = 0; i < tags.length; i++) {
//            tags[i].src += '?imageView2/2/w/200/format/jpg';
            tags[i].removeAttribute('style');
          }
        });
      }

    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import './day.styl';

  .day ul img, .day h2, .day p:nth-last-child(2), .day p:nth-last-child(1),
  .day [target=_blank] img, .day embed {
    display: none;
  }
</style>



